.filter{
    &__wrapper{
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 290px;
        @media screen and (max-width:1000px){
            max-width: 100%;
        }
    }
    &__container{
        display: flex;
        flex-direction: column;
        width: 100%;
        border-bottom: 1px solid rgba(0,0,0,0.1);
        h1{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &:nth-child(2){
            display: flex;
            flex-direction: row;
            width: 100%;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
        }
    }
    &__sorting{
        display: flex;
        flex-direction: row;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        h1{
            min-width: 10ch;
        }
    }
    &__select{
        height: 26px;
        width: 60%;
        padding: 0 4px;
        outline: none;
        cursor: pointer;
        color: rgba(0,0,0,0.6);
        @media screen and (max-width:1000px){
            width: 100%;
            max-width: 400px;
        }
    }
    &__option{
        display: flex;
        flex-direction: row;
        width: 100%;
        gap: 13px;
        input{
            cursor: pointer;
        }
    }
    &__options{
        display: flex;
        flex-direction: column;
        gap: 18px;
        margin-bottom: 36px;
        &:first-child{
            flex-direction: row;
        }
        @media screen and (max-width:1000px){
            max-width: 400px;
            width: 100%;
        }
    }
    &__buttons{
        display: flex;
        flex-flow: row wrap;
        column-gap: 5px;
        row-gap: 10px;
        margin-bottom: 18px;
    }
    &__clear{
        background-color: #fff;
        border: none;
        display: flex;
        flex-direction: row;
        width: fit-content;
        color: #1E90FF;
        cursor: pointer;
        font-size: 13px;
    }
}

