.books{
    &__wrapper{
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 840px;
        position: relative;
    }
    &__container{
        display: flex;
        flex-direction: column;
        width: 100%;
        
    }
    &__count{
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 14px;
        margin-bottom: 16px;
    }
    &__publications{
        padding-right: 14px;
        border-right: 1px solid rgba(0,0,0,0.2);
    }
    &__more{
        display: flex;
        align-items: center;
        gap: 5px;
        background-color: rgba(0,0,0,0.4);
        color: #fff;
        border-radius: 5px;
        border: none;
        font-size: 13px;
        font-weight: 600;
        padding: 10px 20px;
        width: fit-content;
        cursor: pointer;
        margin: auto;
        margin-bottom: 30px;
    }
}