@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.border{
    border-radius: 5px;
    border: 1px solid rgba(0,0,0,0.2);
    background-color: #fff;
}

.line{
    color: rgba(0,0,0,0.3);
    width: 100%;
}

h1{
    font-size: 16px;
    font-weight: bold;
    font-family: 'Open Sans', sans-serif;
    color: rgba(0,0,0,0.6);
    margin: 36px 0;
    text-transform: capitalize;
}

a{
    font-family: 'Open Sans', sans-serif;
}

p{
    font-size: 13px;
    font-weight: normal;
    font-family: 'Open Sans', sans-serif;
    color: rgba(0,0,0,0.6);
}

.input{
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    color: rgba(0,0,0,0.6);
    font-size: 13px;
    font-weight: normal;
    font-family: 'Open Sans', sans-serif;
}

label{
    color: rgba(0,0,0,0.6);
    font-size: 13px;
    font-weight: normal;
    font-family: 'Open Sans', sans-serif;
}

.icon{
    width: 15px;
    height: 15px;
}