.book{
    &__container{
        display: flex;
        flex-direction: row;
        padding-bottom: 53px;
        border-bottom: 1px solid rgba(0,0,0,0.1);
        margin-bottom: 32px;
        gap: 32px;
        img{
            width: 100%;
            max-width: 150px;
            height: 200px;
            cursor: pointer;
        }
        &:last-child{
            border: none;
            margin-bottom: 0;
        }
    }
    &__info{
        display: flex;
        flex-direction: column;
        h1{
            margin-top: 0;
            margin-bottom: 21px;
            text-transform:capitalize;
        }
        p{
            max-width: 200ch;
            margin-bottom: 36px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}