.indexpage{
    &__container{
        display: flex;
        flex-direction: row;
        width: 100%;
        max-width: 1260px;
        margin: 0 auto;
        margin-top: 120px;
        gap: 50px;
        padding: 0 15px;
        @media screen and (max-width: 1000px){
            margin-top: 60px;
            justify-content: center;
            flex-wrap: wrap;
        }
    }
}

    