.header{
    &__wrapper{
        display: flex;
        width: 100%;
        height: 80px;
        background-color: #D9D9D9;
    }   
    &__container{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        height: 80px;
        max-width: 1260px;
        margin: 0 auto;
        padding: 0 15px;
        align-items: center;
        @media screen and (max-width:600px){
            justify-content: center;
        }
    }
    &__logo{
        width: 100%;
        height: 100%;
        max-width: 180px;
        max-height: 60px;
        user-select: none;
        cursor: pointer;
        @media screen and (max-width:600px){
            display: none;
        }
    }
    &__searcher{
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
        max-width: 425px;
        height: 40px;
        padding: 0 12px;
    }
    &__input{
        width: 100%;
    }
}

.search{
    width: 15px;
    height: 15px;
    color: rgba(0,0,0,0.6);
    cursor: pointer;
}