.button{
    &__container{
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 5px;
        background-color: #1E90FF;
        border-radius: 5px;
        width: 100%;
        max-width: fit-content;
        height: 32px;
        color: white;
        padding: 8px 13px;
        p{
            font-weight: bold;
            color: white;
        }
    }
}