.bookpage{
    &__wrapper{
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 1260px;
        padding: 0 15px;
        margin: 0 auto;
        margin-top: 120px;
    }
    &__container{
        width: 100%;
        width: 100%;
        margin: 0 auto;
        display: flex;
        gap: 75px;
        margin-bottom: 160px;
        h1{
            margin: 0;
            margin-bottom: 12px;
        }
        img{
            width:100%;
            max-width: 284px;
            height: 373px;
        }
        @media screen and (max-width:1000px){
            flex-direction: column;
            align-items: center;
            margin-bottom: 100px;
        }
    }
    &__info{
        display: flex;
        flex-direction: column;
        gap: 20px;
        max-width: 503px;
        width: 100%;
        @media screen and (max-width:1000px){
            h1{
                text-align: center;
            }
        }
    }
    &__image{
        display: inline-block;
        width: 100%;
        max-width: 300px;
    }
    &__description{
        line-height: 20px;
        color: rgba(0,0,0,0.7);
        span{
            font-weight: bold;
            cursor: pointer;
        }
    }
    &__similar{
        display: flex;
        flex-direction: column;
        border-left: 1px solid rgba(0,0,0,0.1);
        padding-left: 25px;
        gap: 20px;
        width: 100%;
        h1{
            margin-bottom: 12px;
        }
        @media screen and (max-width:1000px){
            border: none;
            padding: 0;
            text-align: left;
            align-items: center;
        }
    }
    &__read{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 95px;
        background-color: rgba(0,0,0,0.4);
        color: #fff;
        font-weight: bold;
        padding: 8px 20px;
        border-radius: 2px;
        font-size: 14px;
        text-decoration: none;
        @media screen and (max-width:1000px){
            margin: auto;
            margin-top: 25px;
        }
    }
    &__authorbook{
        cursor: pointer;
        color: rgba(0,0,0,0.6);
        text-decoration: none;
        font-size: 13px;
        &:hover{
            transition: 0.2s ease-in-out;
            color: rgba(0,0,0,0.8);
        }
        @media screen and (max-width: 1000px){

        }
    }
    &__books{
        display: flex;
        flex-direction: row;
        gap: 72px;
        width: 100%;
        align-items: center;
        justify-content: center;
        margin-bottom: 60px;
        @media screen and (max-width:1000px){
            flex-direction: column;
            margin-bottom: 30px;
        }
    }
    &__book{
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 100px;
        gap: 18px;
        cursor: pointer;
        p{
            font-size: 11px;
            @media screen and (max-width:1000px){
                font-size: 13px;
                text-align: center;
            }
        }
        img{
            display: flex;
            width: 100%;
            height: 134px;
            @media screen and (max-width:1000px){
                height: 100%;
            }
        }
        @media screen and (max-width:1000px){
            width: 100%;
            max-width: 200px;
        }
    }
}

#manage-button{
    width: fit-content;
    cursor: pointer;
}